import _ from 'lodash'

export default {
  data() {
    return {
      showDialog: false,
      currPage: 1,
      currPageSize: 10,
      inputSearch: '',
      search: '',
      sort: {
        prop: null,
        order: null
      },
    }
  },

  created() {
    this.onInput = _.debounce(() => {
      this.search = this.inputSearch
    }, 300)
  },

  computed: {
    tableData() {
      return this.data.reduce((arr, d) => {
        if (arr.length === 0) {
          arr.push([])
        } else {
          for (let i = 0; i < arr.length; i++) {
            if (arr[i].length === this.currPageSize) {
              if (!arr[i + 1]) {
                arr.push([])
              }
            }
          }
        }

        for (let i = 0; i < arr.length; i++) {
          if (arr[i].length < this.currPageSize) {
            arr[i].push(d)
            break
          }
        }

        return arr
      }, [])
    }
  },

  methods: {
    onInput() {},

    handleSizeChange(pageSize) {
      this.currPageSize = pageSize
    },

    onSortChange({ prop, order }) {
      this.sort = {
        prop,
        order: order ? order === 'ascending' ? 'asc' : 'desc' : null
      }
    }
  }
}


